.item-counter {
  display: flex;
}

.countNo {
  margin: 15px;
}

@media (max-width: 425px) {
  .item-counter > .button {
    line-height: 1;
    height: 35px;
    width: 35px;
    font-size: 14px;
    padding: 7px 14px;
    margin: 5px 5px 5px 5px;
    border-radius: 5px;
    cursor: pointer;
    opacity: 0.7;
    transition: all 0.5s;
    &--danger {
      width: 68px;
      height: 40px;
    }
  }
}
